:root {
  --font-figtree: "figtree", sans-serif;
}

:root {
  --black: hsl(0, 0%, 7%);
  --white: hsl(0, 0%, 100%);
  --grey: hsl(0, 0%, 50%);
  --yellow: hsl(47, 89%, 63%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-figtree);
  background-color: var(--yellow);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-weight: 800;
  font-size: 1.5rem;
  color: var(--black);
}

p {
  margin: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.wrapper .container {
  max-width: 24rem;
  padding: 1.5rem;
  border-radius: 1.25rem;
  background-color: var(--white);
}
.wrapper .container__image img {
  display: block;
  max-width: 100%;
  height: auto;
  border-radius: 0.625rem;
}
.wrapper .container__text {
  padding-top: 1.5rem;
  text-align: start;
  margin-bottom: 1.5rem;
  /*span {
    font-size: rem(14);
    font-weight: 600;
  }*/
}
.wrapper .container__text-category {
  padding: 0.25rem;
  margin-bottom: 0.75rem;
  border-radius: 0.25rem;
  background-color: var(--yellow);
  width: 5.125rem;
  text-align: start;
}
.wrapper .container__text-category span {
  font-size: 0.875rem;
  font-weight: 800;
  color: var(--black);
}
.wrapper .container__text h1 {
  margin-block: 0.75rem;
}
.wrapper .container__text p {
  font-size: 1rem;
  color: var(--grey);
}